.chatBG {
  background: lightblue url("../../assets/bgImg.jpg") fixed center;
  background-color: #cccccc;
  height: auto;
  min-height: 100vh;
}
input[type="text"] {
  background-color: lightgrey;
  border: 0;
  border-radius: 0;
}

.speechBubble {
  position: relative;
  background: lightgray;
  border-radius: 0;
}

.speechBubble:after {
  content: "";
  position: absolute;
  left: 0;
  top: 50%;
  width: 0;
  height: 0;
  border: 20px solid transparent;
  border-right-color: lightgray;
  border-left: 0;
  margin-top: -20px;
  margin-left: -20px;
}
.myBGColor {
  background: lightgray;
}
.myOverFlow {
  height: 70vh;
  overflow-y: auto;
  overflow-x: hidden;
}
